html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.2;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #102654;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scroll-behavior: smooth;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #102654;
  text-decoration: none;
  user-select: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  user-select: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0;
}
@media (max-width: 1199px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.main-button {
  display: inline-block;
  color: #ffffff;
  font-size: 12px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  text-align: center;
  background-color: transparent;
  cursor: pointer;
  border: 4px solid #ffffff;
  padding: 20px 52px;
}
@media (max-width: 1199px) {
  .main-button {
    padding: 20px 29px;
  }
}
.main-button.active {
  color: #103aae;
  background-color: #ffffff;
}
@media (pointer: fine) {
  .main-button:hover:not([disabled]) {
    color: #103aae;
    background-color: #ffffff;
  }
}
.main-button:focus-visible:not([disabled]) {
  color: #103aae;
  background-color: #ffffff;
}
.main-button:active:not([disabled]) {
  color: #102654;
  background-color: #ffffff;
}
.main-button[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}
.main-button--dark {
  color: #003d85;
  border-color: #003d85;
}
.main-button--dark.active {
  color: #ffffff;
  border-image: linear-gradient(118deg, #ff1553, #003d85) 1;
  background-image: linear-gradient(118deg, #ff1553, #003d85);
}
@media (pointer: fine) {
  .main-button--dark:hover:not([disabled]) {
    color: #ffffff;
    border-image: linear-gradient(118deg, #ff1553, #003d85) 1;
    background-image: linear-gradient(118deg, #ff1553, #003d85);
  }
}
.main-button--dark:focus-visible:not([disabled]) {
  color: #ffffff;
  border-image: linear-gradient(118deg, #ff1553, #003d85) 1;
  background-image: linear-gradient(118deg, #ff1553, #003d85);
}
.main-button--dark:active:not([disabled]) {
  color: rgba(255, 255, 255, 0.7);
  border-image: linear-gradient(118deg, #ff1553, #003d85) 1;
  background-image: linear-gradient(118deg, #ff1553, #003d85);
}

